import React from 'react';
import MapData from './MapData.js'
import BaseMap from './BaseMap.js'
//import InfoPanel from './InfoPanel.js'
import DetailView from './DetailView.js'
import TopBar from './TopBar.js'

class MapContainer extends React.Component {
  constructor (props) {
    super(props)

    this.mapData = new MapData()
    this.pointsById = {}
    this.state = {
      geoJson: {},
      // mapCenter: [],
      // textos: [],
      // userLocation: [],
      // selected: null,
      loaded: false,
      bounds: null,
      center: {lng: -74.065604, lat: 4.652280},
      selectedPoint: null,
      selectedIndex: 0,
      query: null,
      animate: false,
      searchResults: []
    //  styleURL: this._mapOptions[0].data,
    }
    //this.updateBounds = this.updateBounds.bind(this)
    this.updateSelectedPoint = this.updateSelectedPoint.bind(this)
    // this.getNextTexto = this.getNextTexto.bind(this)
    // this.getPreviousTexto = this.getPreviousTexto.bind(this)
    this.searchMapData = this.searchMapData.bind(this)
    this.getNextInCollection = this.getNextInCollection.bind(this)
    this.getPreviousInCollection = this.getPreviousInCollection.bind(this)
    this.clearSearch = this.clearSearch.bind(this)
  }

  updateSelectedPoint(point, animate) {
    console.log('selected point', point, animate)
    this.setState({selectedPoint: point, animate: animate})
  }

  clearSearch () {
    this.setState({ searchResults: [], query: ''})
  }
  searchMapData(query) {
    var results = this.mapData.searchData(query).map((result, index) => {
      result.index = index
      return result
    })
    console.log('results', query, results)
    this.setState({ searchResults: results, query: query}, () => {
      if(results.length > 0) {
        var index = 0
        var point = this.pointsById[this.state.searchResults[index].ref]
        this.setState( { selectedIndex: index, selectedPoint: point, animate: true})
      }
    })

  }

  // getNextTexto(point) {
  //   var index = point.index
  //   index++
  //   if(index >= this.state.geoJson.features.length) index = 0
  //   console.log('features', this.state.geoJson.features, 'index', index, this.state.geoJson.features[index])
  //   this.setState({ selectedPoint: this.state.geoJson.features[index], animate: true})
  // }
  //
  // getPreviousTexto(point) {
  //   var index = point.index
  //   index--
  //   if(index < 0) index = this.state.geoJson.features.length - 1
  //   this.setState({ selectedPoint: this.state.geoJson.features[index], animate: true})
  // }

  getNextInCollection() {
    var index = this.state.selectedIndex + 1
    if(index >= this.state.searchResults.length) {
      index = 0
    }
    var point = this.pointsById[this.state.searchResults[index].ref]
    this.setState( { selectedIndex: index, selectedPoint: point, animate: true})
  }

  getPreviousInCollection() {
    var index = this.state.selectedIndex - 1
    if(index < 0) {
      index = this.state.searchResults.length - 1
    }
    var point = this.pointsById[this.state.searchResults[index].ref]
    this.setState( { selectedIndex: index, selectedPoint: point, animate: true})
  }
  // updateBounds(newBounds, center) {
  //   console.log('new bounds', newBounds, center)
  //   this.setState({bounds: newBounds})
  // }

  componentDidMount () {
    this.mapData.loadData().then(()=>{
    //  console.log(this.mapData.getGeoJson())
      var data = this.mapData.getTextosGeoJson()
      data.features.forEach((texto) => {
          this.pointsById[texto.uniqueId] = texto
      })

      //this.mapData.getGeoJson()
      this.setState({ geoJson : data, loaded: true})
      //console.log("data loaded")
    })
  }
  // <InfoPanel geoJson={this.state.geoJson} data={this.mapData} loaded={this.state.loaded} bounds={this.state.bounds}/>
  // <DetailView point={this.state.selectedPoint} />
  //updateBounds={this.updateBounds}
  render () {
    return <div>
      <BaseMap
        geoJson={this.state.geoJson}
        loaded={this.state.loaded}
        updateSelectedPoint={this.updateSelectedPoint}
        animate={this.state.animate}
        center={this.state.center}
        selectedPoint={this.state.selectedPoint === null ? null : this.state.selectedPoint.uniqueId}
        searchResults = {this.state.searchResults}
      />
      <DetailView
        point={this.state.selectedPoint}
        searchMapData = {this.searchMapData}
      />
      <TopBar
        query={this.state.query}
        searchMapData={this.searchMapData}
        clearSearch={this.clearSearch}
        getNextInCollection = {this.getNextInCollection}
        selectedIndex = {this.state.selectedIndex}
        searchResults = {this.state.searchResults}
        getPreviousInCollection = {this.getPreviousInCollection}
      />
    </div>
  }
}

export default MapContainer
