import React from 'react';
import logo from './../assets/logo-alcaldia.png'

class DetailView extends React.Component {

  render () {
    var innerInfo = null
    if(this.props.point && this.props.point!== null) {
      var point = this.props.point.properties

      // Replace <b> </b> tags indicating pont location with link element
      var texto = point.textoCorto
      var start = texto.indexOf("<b>")
      var end = texto.indexOf("</b>")
      var textoEl = null
      if(start >= 0 && end >= 0) {
        var startTexto = texto.substring(0, start)
        var linkTexto = texto.substring(start + 3, end)
        var endTexto = texto.substring(end + 4, texto.length)
        // console.log('SUBSTRING', startTexto, 'LINK', linkTexto, 'END', endTexto)

        textoEl =   <div className="h-1/2 p-4 text-black leading-normal pb-3 font-sans overflow-auto" style={{ flex:2 }}>
          <span> {'"' + startTexto} </span>
          <span className="text-white pointer-events-auto cursor-pointer hover:text-black" onClick={()=> { this.props.searchMapData(point.punto) }}> {linkTexto} </span>
          <span> {endTexto + '"'} </span>
        </div>
      } else {
        textoEl = <div className="h-1/2 p-4 text-black leading-normal pb-3 font-sans overflow-auto" style={{ flex:2 }}>
            "<span dangerouslySetInnerHTML={{__html: point.textoCorto}}></span>"
        </div>
      }

      innerInfo =  <div className = "p-2 pt-0 md:pt-10 flex flex-col sm:flex-row flex items-start md:items-center">
        <div className="max-w-xl flex flex-col sm:flex-row flex items-start md:items-center h-full">
          {textoEl}
          <div className="p-4 text-large font-sans text-white flex-1 opacity-100">
              <div
                className="text-lg md:text-4xl sm:mb-6 cursor-pointer hover:text-black pointer-events-auto"
                onClick={()=> { this.props.searchMapData(point.libro) }}
                >
                  {point.libro}
              </div>
              <div className="cursor-pointer hover:text-black pointer-events-auto" onClick={()=> { this.props.searchMapData(point.autor) }}>{point.autor} </div>
              <div className="cursor-pointer hover:text-black italic pointer-events-auto" onClick={()=> { this.props.searchMapData(point.punto) }}>{point.punto} </div>
          </div>
        </div>
      </div>
    } else {
      innerInfo =
        <div className="max-w-xl h-full flex items-center">
          <div className="p-4 text-4xl font-sans text-white">
                  Recorridos Literarios
          </div>
        </div>
    }

    return <div className="pointer-events-none  absolute w-full pin-b pin-l pt-8 md:pt-20 flex justify-center gradient" style={{ height: "50%"}}>
      {innerInfo}
        <img className="absolute pin-b pin-r m-4" src={logo} style={{ width: '100px'}}/>
      </div>

  }
}

export default DetailView
